<template>
    <div class="register container">
        <div class="row mt-3">
            <div class="col-md-12 col-lg-12">
                <div class="card">
                    <div class="card-body">
                        <img  src="../assets/icon.png" class="mx-auto d-block">
                        <form @submit.prevent="onSubmit">
                            <div class="form-group">
                                <label for="email">邮箱</label>
                                <input 
                                type="email" 
                                class="form-control"
                                v-model="email"
                                >
                            </div>
                            <div class="form-group">
                                <label for="password">密码</label>
                                <input 
                                type="password" 
                                class="form-control"
                                v-model="password"
                                >
                            </div>
                            <div class="form-group">
                                <label for="confirm-password">确认密码</label>
                                <input 
                                type="password" 
                                class="form-control"
                                v-model="confirmPassword"
                                >
                            </div>
                            <button class="btn btn-block btn-info" @click.prevent="submitted">提交</button>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data () {
        return {
            email : null,
            password : null,
            confirmPassword : null,
        }
    },
    methods: {
        submitted () {
            if(this.email == null || this.password == null || this.confirmPassword == null) {
                alert("请将信息填写完整");
            }else{
                if(this.password === this.confirmPassword) {
                    let information = {
                        email : this.email,
                        password : this.password
                    };
                    this.http.post("users.json",information)
                        .then( res => {
                            this.$router.push({
                                name : "loginLink"
                            })
                        });
                }else{
                    alert("两次输入密码不一致");
                }
            }
        }
    }
}
</script>




