<template>
    <div class="register container">
        <div class="row mt-3">
            <div class="col-md-12 col-lg-12">
                <div class="card">
                    <div class="card-body">
                        <img  src="../assets/icon.png" class="mx-auto d-block">
                        <form @submit.prevent="onSubmit">
                            <div class="form-group">
                                <label for="email">邮箱</label>
                                <input 
                                type="email" 
                                class="form-control"
                                v-model="email"
                                >
                            </div>
                            <div class="form-group">
                                <label for="password">密码</label>
                                <input 
                                type="password" 
                                class="form-control"
                                v-model="password"
                                >
                            </div>
                            <button class="btn btn-block btn-success" @click.prevent="login">登录</button>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data () {
        return {
            email : null,
            password : null
        }
    },
    beforeRouteEnter (to, from, next) {
        // 退出其实不是退出，是再次进入登录界面，每次进入登录界面都把islogin变为fasle --> 给whetherLogin 传null
        next( vm => {
            vm.$store.commit("whetherLogin",null);
            // vm.$store.commit("setLogin",false);
        })
    },
    methods: {
        login () {
            this.http.get("users.json")
                .then( res => {
                    let userInformation = [];
                    for(let key in res.data) {
                        if(res.data[key].email === this.email && res.data[key].password === this.password) {
                            userInformation.push(res.data[key]);
                        }
                    }
                    if(userInformation.length > 0 && userInformation != null) {
                        alert("登录成功");
                        // this.$store.getters.isLogin = true;，这里不能直接改，因为vuex里面的数据，只能通过提交mutation来修改
                        this.$store.commit("whetherLogin",userInformation);
                        this.$router.push({
                            name : "homeLink"
                        })
                    }else{
                        this.$store.commit("whetherLogin",null);
                        alert("邮箱或密码有误");
                    }
                })
        }
    }
}
</script>




