<template>
    <form>
        <h3 class="text-muted my-5">添加新的pizza</h3>
        <div class="form-group row">
            <label class="col-sm-1">品种</label>
            <div class="col-sm-11">
                <input type="text" class="form-control" v-model="newPizzas.name">
            </div>
        </div>
        <div class="form-group row">
            <label class="col-sm-1">描述</label>
            <div class="col-sm-11">
                <textarea rows="5" class="form-control" v-model="newPizzas.description"></textarea>
            </div>
        </div>
        <p><strong>选项一</strong></p>
        <div class="form-group row">
            <label class="col-sm-1">尺寸</label>
            <div class="col-sm-11">
                <input type="text" class="form-control" v-model="newPizzas.size1">
            </div>
        </div>
        <div class="form-group row">
            <label class="col-sm-1">价格</label>
            <div class="col-sm-11">
                <input type="text" class="form-control" v-model="newPizzas.price1">
            </div>
        </div>
        <p><strong>选项二</strong></p>
        <div class="form-group row">
            <label class="col-sm-1">尺寸</label>
            <div class="col-sm-11">
                <input type="text" class="form-control" v-model="newPizzas.size2">
            </div>
        </div>
        <div class="form-group row">
            <label class="col-sm-1">价格</label>
            <div class="col-sm-11">
                <input type="text" class="form-control" v-model="newPizzas.price2">
            </div>
        </div>
        <div class="form-group row">
            <button class="btn btn-success btn-block" @click.prevent="addMenuItem">添加</button>
        </div>
    </form>
</template>
<script>
export default {
    data () {
        return {
            newPizzas : {}
        }
    },
    methods: {
        addMenuItem () {
            let pizza = {
                name : this.newPizzas.name,
                description : this.newPizzas.description,
                options : [
                    {
                        size : this.newPizzas.size1,
                        price : this.newPizzas.price1,
                    },
                    {
                        size : this.newPizzas.size2,
                        price : this.newPizzas.price2
                    }
                ]
            }
            this.http.post('/menu.json',pizza)
                .then( res => {
                    this.$store.commit("addPizza",pizza);
                    this.$router.push({
                        name : 'menuLink'
                    })
                });
        }
    }
}
</script>




