// 改变属性的状态
export const getMenu = (state, payload) => {
    // 进入到admin里面，pizzaMenuItems是数组
    // 进到menu里面。pizzaMenuItems是对象，两者互补印象
    // 野狗里面的数据是改不了的，只是把从野狗拿到的数据加工
    state.pizzaMenuItems = payload;
}
export const deletePizza = (state, id) => {
    state.pizzaMenuItems.forEach( (pizza,index) => {
        if(pizza.id == id) {
            state.pizzaMenuItems.splice(index,1);
        }
    })
}
export const addPizza = (state, payload) => {
    state.pizzaMenuItems.push(payload);
}
export const whetherLogin = (state,payload) => {
    if(payload) {
        // payload 有值就证明登录成功
        state.isLogin = true;
        state.currentUser = payload[0].email;
    }else{
        state.isLogin = false;
        state.currentUser = null;
    }
}
export const setLogin = (state, status) => {
    state.isLogin = status
  }
 


