<template>
    <div class="home container">
        <div class="row">
            <div class="col-sm-12 text-center" id="home">
                <div class="background">
                    <h1>欢迎大家品尝pizza！</h1>
                    <h2>这里有你非常喜欢的pizza</h2>
                    <button class="btn btn-success" @click="goToMenu">let us order pizza</button>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name : "home",
    methods: {
        goToMenu () {
            this.$router.push({
                name : "menuLink"
            })
        }
    }
}
</script>
<style scoped>
#home{
    background: url('../assets/pizza.jpg');
    height: 85vh;
    padding: 10%
}
h1,h2{
    margin: 6%;
}
.background{
    background: #eee;
    opacity: 0.8;
    max-width: 70vw;
    margin: 0 auto;
    padding: 20px 0;
}
</style>



