import Vue from 'vue'
import Vuex from 'vuex'
import * as getters from './getters'
import * as mutations from './mutations'
Vue.use(Vuex)
export default new Vuex.Store({
    state : {
        pizzaMenuItems : {},
        isLogin : false,
        currentUser : null
    },
    getters,
    mutations
})


