import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios'
import store from './store/detachment/'
Vue.config.productionTip = false
axios.defaults.baseURL = "https://wd3466994176ebcjbp.wilddogio.com/"
Vue.prototype.http = axios

router.beforeEach((to, from, next) => {
  if(!store.getters.isLogin) {
    if(to.path == '/login' || to.path == '/register') {
      next();
    }else{
      alert("还没有登录，清先登录");
      next("/login");
    }
  }else{
    next();
  }    
})
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



