<template>
    <div class="app-menu container row">
        <!-- 菜单 -->
        <div class="col-sm-12 col-md-8">
            <table class="table">
                <thead class="thead default">
                    <tr>
                        <th>尺寸</th>
                        <th>价格</th>
                        <th>加入</th>
                    </tr>
                </thead>
                <tbody v-for="pizza in pizzaMenu" :key="pizza.name">
                    <tr>
                        <td>
                            <strong>{{pizza.name}}</strong>
                        </td>
                    </tr>
                    <tr v-for="pizzaOption in pizza.options" :key="pizzaOption.price">
                        <td>{{pizzaOption.size}}</td>
                        <td>{{pizzaOption.price}}</td>
                        <td><button class="btn btn-sm btn-outline-success" @click="addToBasket(pizza,pizzaOption)">+</button></td>
                    </tr>
                </tbody>
            </table>
        </div>
        <!-- 购物车 -->
        <div class="col-md-4 col-sm-12">
            <div v-if="baskets.length > 0">
                <div>
                <table class="table">
                    <thead class="thead-default">
                        <tr>
                            <th>数量</th>
                            <th>品种</th>
                            <th>价格</th>
                        </tr>
                    </thead>
                    <tbody v-for="basket in baskets">
                        <tr>
                            <td>
                            <button class="btn btn-sm" @click="decreaseQuanlity(basket)">-</button>
                                    <span>{{basket.quanlity}}</span>
                            <button class="btn btn-sm" @click="increaseQuanlity(basket)">+</button>  
                            </td>
                            <td>{{basket.name}} {{basket.size}}</td>
                            <td>{{basket.price}}</td>
                        </tr>
                    </tbody>
                </table>
                <p>总价：{{total}} 元</p>
                <button class="btn btn-success btn-block">提交</button> 
            </div>
            </div>
            <div v-else>
                <div class="alert alert-success">
                    {{basketMsg}}
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data () {
        return {
            baskets : [],
            basketMsg : "购物车中没有任何商品",
        }
    },
    computed: {
        pizzaMenu () {
            // console.log(this.$store.getters.getPizzas); 在admin里面修改的数据状态根本就不会影响到我这个页面的数据，因为就算你在admin里面修改了数据，我每次进入到menu页面之前，我都会从野狗里面拿到数据，然后传给vuex，注意，我这里是从野狗拿到的数据，admin里面只是把从野狗拿到的数据加工了一下，并不是修改了野狗的数据
            return this.$store.getters.getPizzas;
        },
        total () {
            let totalCost = 0;
            this.baskets.forEach( basket => {
                totalCost += basket.price * basket.quanlity;
            });
            return totalCost;
        }
    },
    methods: {
        fetchPizzas () {
            this.http.get('menu.json')
                .then( res => this.$store.commit("getMenu",res.data))
        },
        increaseQuanlity (basket) {
            basket.quanlity ++;
        },
        decreaseQuanlity (basket) {
            basket.quanlity --;
            if(basket.quanlity < 1) {
                this.removeBasket(basket);
            }
        },
        removeBasket (basket) {
            this.baskets.splice(this.baskets.indexOf(basket),1);
        },
        addToBasket (pizza,pizzaOption) {
            let basket = {
                name : pizza.name,
                quanlity : 1,
                size : pizzaOption.size,
                price : pizzaOption.price
            };
            if(this.baskets.length > 0 && this.baskets != null) {
                let existPizza = this.baskets.filter( basket => {
                    return pizza.name === basket.name && pizzaOption.price === basket.price
                });
                if(existPizza.length > 0 && existPizza != null) {
                   existPizza[0].quanlity ++;
                }else{
                    this.baskets.push(basket);
                }
            }else{
                this.baskets.push(basket);
            }

            
        }
    },
    created () {
        this.fetchPizzas();
    }
}
</script>




