<template>
    <div class="admin container">
        <div class="row">
            <div class="col-sm-12 col-md-8">
                <!-- 左边新的添加pizza -->
                <add-new-pizza></add-new-pizza>
            </div>
            <div class="col-sm-12 col-md-4">
                <!-- 品种展示 -->
                <h3 class="text-muted my-5">菜单</h3>
                <table class="table">
                    <thead class="table table-default">
                        <tr>
                            <th>品种</th>
                            <th>删除</th>
                        </tr>
                    </thead>
                    <tbody v-for="pizza in pizzaMenu" :key="pizza.key">
                        <tr>
                            <td>
                                {{pizza.name}}
                            </td>
                            <td>
                                <button class="btn btn-outline-danger btn-sm" @click="deletePizza(pizza.id)">&times;</button>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </div>
</template>
<script>
import AddNewPizza from './AddNewPizza'
export default {
    computed: {
        pizzaMenu () {
            return this.$store.getters.getPizzas;
        }
    },
    methods: {
        changeMenu () {
            let newPizzaMenu = [];
            this.http.get('menu.json')
                .then( res => {
                    for(let key in res.data) {
                        res.data[key].id = key;
                        newPizzaMenu.push(res.data[key]);
                    };
                    this.$store.commit("getMenu",newPizzaMenu);
                })
            
        },
        deletePizza (id) {
            this.http.delete('menu/' + id + '.json')
                .then( res => {
                    this.$store.commit("deletePizza",id);
                });
        }
    },
    created () {
        this.changeMenu();
    },
    components: {
        AddNewPizza
    }
}
</script>




